/* Set defaults */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html,
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', 'Open Sans', 'Arial', sans-serif;
  max-width: 1920px;
  color: #fff;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  background-color: #121314;
}

::-webkit-scrollbar {
  width: 10px;
  height: 5px;
  background-color: #222;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #c50c0c;
}

img,
video {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

/* Atomic */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

.d-f {
  display: flex;
}

.ttu {
  text-transform: uppercase;
}

.ta-c {
  text-align: center;
}

.ov-h {
  overflow: hidden;
}

.ov-s {
  overflow: scroll;
}

.black {
  color: #000;
}

/* Global */

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
}

.sr-only,
.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  appearance: none;
  display: inline-block;
  background-color: #c50c0c;
  color: #fff;
  border: none;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  padding: 25px 40px;
  text-transform: uppercase;
  line-height: 1;
}

.btn:hover,
.btn:active,
.btn:focus {
  background-color: #ae0b0b;
  transition: background-color 0.5s;
}

.main-heading,
.page-heading {
  font-size: 48px;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
}

.main-heading_404 {
  padding-top: 200px;
  padding-bottom: 200px;
  text-align: center;
  font-size: 6vw;
}

.page-heading {
  font-size: 36px;
}

.link-more {
  font-weight: 600;
  font-size: 16px;
  padding-right: 35px;
  display: inline-block;
  color: #c50c0c;
  background-image: url(../img/icons/arrow_red.png);
  background-size: 21px 10px;
  background-repeat: no-repeat;
  background-position: right center;
}

.link-more:hover,
.link-more:active,
.link-more:focus {
  color: #fff;
  transition: color 0.5s;
  background-image: url(../img/icons/arrow_white.png);
}

.link-more_inverse {
  background-image: url(../img/icons/arrow_black.png);
}

.price {
  font-size: 48px;
  font-weight: 800;
  color: #fff;
}

.price__unit {
  color: #999999;
  font-size: 18px;
  font-weight: 400;
}

.inner .main-content {
  background-image: url(../img/inner_bg.jpg);
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 140px;
  padding-bottom: 140px;
}

.inner .cards {
  background-image: unset;
}

.inner .cards__h {
  margin-bottom: 45px;
}

.like {
  padding-left: 20px;
  background-image: url(../img/svg/like.svg);
  background-size: 15px;
  background-position: left center;
  background-repeat: no-repeat;
}

/* General */

.main-header {
  padding-top: 20px;
  background-color: transparent;
  height: 100px;
  margin-bottom: -100px;
  position: relative;
  z-index: 2;
}

.main-header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logolink,
.main-header__logolink img {
  display: block;
}

.main-header__widget {
  margin-left: 30px;
}

.main-navigation {
  margin: 0 10px;
}

.main-navigation__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  align-items: baseline;
}

.main-navigation__list li {
  padding-bottom: 10px;
  border-bottom: 1px solid transparent;
}

.main-navigation__list li + li {
  margin-left: 30px;
}

.main-navigation__list a {
  color: #fff;
}

.main-navigation__list li.active,
.main-navigation__list li.current-menu-item,
.main-navigation__list li:hover {
  border-bottom-color: #c50c0c;
}

.inner .main-header {
  border-bottom: 1px solid #131313;
}

.widget-contacts {
  display: block;
}

.widget-contacts__phone {
  font-weight: 500;
  line-height: 1;
  color: #fff;
  font-size: 24px;
  display: block;
  padding-top: 10px;
  padding-bottom: 5px;
  padding-right: 35px;
  background-image: url(../img/svg/phone.svg);
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: right center;
}

.widget-contacts__phone:hover,
.widget-contacts__phone:focus,
.widget-contacts__phone:active {
  color: #777;
  transition: color 0.5s;
}

.widget-contacts__address {
  margin: 0;
  padding: 0;
  text-align: center;
  color: #999999;
  font-size: 12px;
}

.footer .widget-contacts__phone {
  padding-right: 0;
  padding-left: 35px;
  background-position: left center;
}

.widget-contact-mail {
  font-size: 12px;
}

.widget-contact-mail a {
  color: #999999;
  text-decoration: underline;
}

.widget-contact-mail a:hover,
.widget-contact-mail a:focus,
.widget-contact-mail a:active {
  color: #fff;
  transition: color 0.5s;
}

.widget-social-links {
  display: inline-block;
  vertical-align: middle;
  width: 36px;
  height: 36px;
  background-size: 15px;
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #18191b;
}

.widget-social-links + .widget-social-links {
  margin-left: 15px;
}

.widget-social-links.fb {
  background-image: url(../img/svg/facebook-app-symbol.svg);
}

.widget-social-links.vk {
  background-image: url(../img/svg/vk.svg);
}

.widget-social-links.youtube {
  background-image: url(../img/svg/youtube-symbol.svg);
}

.widget-social-links.insta {
  background-image: url(../img/svg/instagram.svg);
}

.widget-social-links:hover path,
.widget-social-links:focus path,
.widget-social-links:active path {
  fill: #c50c0c;
  transition: fill 0.5s;
}

.widget-address,
.widget-working-time,
.widget-phone,
.widget-email {
  font-size: 16px;
  color: #cccccc;
  padding-left: 30px;
  position: relative;
}

.widget-address:after,
.widget-working-time:after,
.widget-phone:after,
.widget-email:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 25px;
  height: 25px;
  border: none;
  border-radius: 50%;
  background-color: #c50c0c;
  background-repeat: no-repeat;
  background-size: 15px;
  background-position: center;
}

.widget-address:after {
  background-image: url(../img/svg/placeholder-for-map.svg);
}

.widget-working-time:after {
  background-image: url("../img/svg/clock (2).svg");
}

.widget-phone:after {
  background-image: url(../img/svg/smartphone.svg);
}

.widget-email:after {
  background-image: url(../img/svg/mail.svg);
}

.footer {
  padding-top: 50px;
  padding-bottom: 25px;
  background-color: #121314;
}

.footer .main-header {
  margin-bottom: 0;
  padding-top: 5px;
  padding-bottom: 35px;
}

.footer .slogan {
  color: #999999;
  font-weight: 400;
  font-size: 11px;
}

.main-footer {
  border-top: 1px solid #1f2021;
  padding-top: 35px;
}

.main-footer__row {
  align-items: center;
}

.main-footer__widget {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  color: #999999;
}

.main-footer__widget_copyright {
  font-size: 14px;
}

.main-footer__widget_social {
  text-align: right;
}

.breadcrumbs {
  margin: 0;
  margin-bottom: 75px;
  padding: 0;
  display: flex;
  align-items: baseline;
}

.breadcrumbs__item {
  font-size: 14px;
  padding-right: 5px;
}

.breadcrumbs__item:last-child a {
  color: #999999;
}

.breadcrumbs__item:last-child a:hover,
.breadcrumbs__item:last-child a:focus,
.breadcrumbs__item:last-child a:active {
  color: #fff;
  transition: color 0.5s;
}

.breadcrumbs__item + .breadcrumbs__item:before {
  content: '/';
  color: #fff;
}

.breadcrumbs__item a:hover,
.breadcrumbs__item a:focus,
.breadcrumbs__item a:active {
  color: #777;
  transition: color 0.5s;
}

.breadcrumbs__link {
  color: #fff;
}

/* index.html */

.main-content {
  position: relative;
  z-index: 1;
  background-attachment: fixed;
}

.banner {
  text-align: center;
  height: 100vh;
  background-image: url(../img/index__firstScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  z-index: 1;
}

.banner__link {
  position: absolute;
  bottom: 12.4vh;
  left: 50%;
  transform: translateX(-50%);
}

.about {
  padding-top: 200px;
  padding-bottom: 200px;
  background-image: url(../img/index__secondScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.about__flex {
  display: flex;
  justify-content: space-between;
}

.about__wrap {
  width: calc(41.66666666666667% - 20px);
}

.about__h {
  margin-bottom: 40px;
}

.about__text {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 55px;
}

.about__thumb {
  background-color: #c50c0c;
  margin-right: 20px;
  margin-top: 20px;
}

.about__thumb img {
  position: relative;
  top: -20px;
  right: -20px;
}

.sales {
  padding-top: 150px;
  padding-bottom: 150px;
  background-image: url(../img/index__redScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.sales__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 70px;
}

.sales__btns {
  display: flex;
  justify-content: space-between;
  width: 140px;
}

.sales__btn {
  border: none;
  width: 60px;
  height: 60px;
  background-color: #000;
  position: relative;
  cursor: pointer;
}

.sales__btn:before {
  content: '';
  display: block;
  width: 18px;
  height: 18px;
  border-top: 5px solid #fff;
  border-right: 5px solid #fff;
  position: absolute;
  top: 50%;
  left: 50%;
}

.sales__btn_next:before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.sales__btn_prev:before {
  transform: translate(-50%, -50%) rotate(-135deg);
}

.sales__btn:hover,
.sales__btn:focus,
.sales__btn:active {
  background-color: #fff;
  transition: 0.5s;
}

.sales__btn:hover:before,
.sales__btn:focus:before,
.sales__btn:active:before {
  border-color: #000;
  transition: border-color 0.5s;
}

.sales__slider {
  display: flex;
  justify-content: space-between;
}

.slider {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.slider__slide {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(50% - 20px);
}

.stock__link {
  display: block;
}

.stock__thumb {
  width: 100%;
  margin-bottom: 50px;
}

.stock__h {
  font-size: 24px;
  font-weight: 600;
  color: #fff;
  margin-bottom: 40px;
}

.stock__h:hover,
.stock__h:focus,
.stock__h:active {
  color: #ddd;
  transition: color 0.5s;
}

.stock__text {
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  margin-bottom: 35px;
}

.stock__text:hover,
.stock__text:focus,
.stock__text:active {
  color: #ddd;
  transition: color 0.5s;
}

.stock__more {
  color: #000;
}

.stock__more:hover,
.stock__more:focus,
.stock__more:active {
  color: #fff;
  transition: color 0.5s;
  background-image: url(../img/icons/arrow_white.png);
}

.cards {
  padding-top: 160px;
  padding-bottom: 160px;
  background-image: url(../img/index__finalScreen_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.cards__h {
  margin-bottom: 80px;
}

.cards__list {
  list-style: none;
}

.card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  padding-top: 50px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 190px;
  margin-bottom: 35px;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.card:nth-child(1) {
  background-image: url(../img/index__cards_card1.jpg);
}

.card:nth-child(2) {
  background-image: url(../img/index__cards_card2.jpg);
}

.card:nth-child(3) {
  background-image: url(../img/index__cards_card3.jpg);
}

.card_profitable .card__name {
  color: #c50c0c;
}

.card__name {
  font-size: 27px;
  font-weight: 800;
  text-align: center;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.card__time {
  font-size: 16px;
  font-weight: 400;
  color: #999999;
  text-align: center;
  margin-bottom: 50px;
}

.card__price {
  padding-bottom: 45px;
  text-align: center;
  margin: 0;
}

.card__features {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}

.card__feature {
  font-size: 16px;
  font-weight: 400;
  padding-left: 25px;
  background-image: url(../img//icons/checkmark.png);
  background-repeat: no-repeat;
  background-size: 14px 14px;
  background-position: left center;
}

.card__feature + .card__feature {
  margin-top: 20px;
}

.card__buy {
  position: absolute;
  bottom: 0;
  left: 50%;
  padding-left: 65px;
  padding-right: 65px;
  transform: translate(-50%, 50%);
}

/* blog.html */

.last-posts {
  padding-bottom: 80px;
}

.last-posts__h {
  margin-bottom: 45px;
}

.posts-list {
  margin: 0;
  padding: 0;
}

.last-post__link {
  display: flex;
}

.last-post__thumb {
  margin: 0;
  padding: 0;
  min-width: 25%;
}

.last-post__img {
  display: block;
}

.last-post__wrap {
  flex-grow: 1;
  padding-left: 25px;
}

.last-post__h {
  font-size: 24px;
  color: #fff;
  font-weight: 800;
  margin-bottom: 35px;
}

.last-post__text {
  margin-bottom: 25px;
  color: #fff;
  font-size: 16px;
  line-height: 25px;
}

.last-post + .last-post {
  margin-top: 30px;
}

.categories__h {
  margin-bottom: 50px;
}

.categories-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.category {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  margin-bottom: 20px;
}

.category__thumb {
  margin-bottom: 20px;
}

.category__name {
  font-size: 24px;
  color: #fff;
  font-weight: 800;
}

/* category.html */

.category-posts__h {
  margin-bottom: 40px;
}

.pagination {
  padding-top: 60px;
}

.pagination__links {
  width: 295px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.pagination__block {
  font-size: 18px;
  width: 40px;
  height: 40px;
  position: relative;
  z-index: 1;
}

.pagination__block_current {
  background-color: #c50c0c;
}

.pagination__link {
  display: block;
  color: #fff;
  position: absolute;
  text-align: center;
  padding: 10px 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
}

/* WP Pagination */

.pagination.navigation {
  color: #fff;
}

.pagination.navigation .nav-links {
  width: 295px;
  margin: 0 auto;
  display: flex;
  align-items: baseline;
}

.pagination.navigation .page-numbers {
  display: block;
  font-size: 18px;
  width: 40px;
  height: 40px;
  color: #fff;
  text-align: center;
  padding: 10px 0;
}

.pagination.navigation .page-numbers.current {
  background-color: #c50c0c;
}

.pagination.navigation .page-numbers.next,
.pagination.navigation .page-numbers.prev {
  display: none;
}

.pagination.navigation .page-numbers + .pagination.navigation .page-numbers {
  margin-left: 20px;
}

/* single.html */

.main-article__header {
  margin-bottom: 40px;
}

.main-article__thumb {
  display: block;
  margin-bottom: 50px;
}

.main-article__h {
  font-size: 48px;
  text-transform: uppercase;
  font-weight: 900;
}

.main-article h2,
.main-article h3 {
  font-size: 24px;
  margin-bottom: 30px;
  font-weight: 800;
}

.main-article h4,
.main-article h5 {
  font-size: 20px;
  margin-bottom: 30px;
  font-weight: 600;
}

.main-article ul {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  margin-bottom: 10px;
}

.main-article li {
  list-style: disc;
}

.main-article li + li {
  margin-top: 5px;
}

.main-article p {
  font-size: 16px;
  line-height: 1.1;
  margin-bottom: 20px;
}

.main-article__footer {
  padding-top: 30px;
  padding-bottom: 1px;
  color: #666666;
  font-size: 16px;
}

.main-article__footer time {
  margin-right: 20px;
}

.main-article__like {
  color: #666666;
}

.main-article__like:hover,
.main-article__like:active,
.main-article__like:focus {
  color: #fff;
  transition: color 0.5s;
}

.main-article__like:hover path,
.main-article__like:active path,
.main-article__like:focus path {
  fill: #fff;
  transition: fill 0.5s;
}

/* contacts.html */

.contacts__h {
  margin-bottom: 45px;
}

.contacts__h_form {
  margin-bottom: 30px;
}

.contacts__info {
  display: flex;
  align-items: center;
  margin-bottom: 70px;
}

.contacts__info > * + * {
  margin-left: 45px;
}

.contacts__form,
.contacts__form p {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.map {
  position: relative;
  z-index: 1;
}

.map iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}

.map__fallback {
  display: block;
  margin-bottom: 30px;
}

.contacts-form__label {
  width: calc(33.33333% - 20px);
}

.contacts-form__label_textarea {
  width: 100%;
}

.contacts-form__input,
.contacts-form__textarea {
  border: none;
  display: block;
  font: inherit;
  width: 100%;
  background-color: #1f2021;
  color: #cccccc;
  margin-bottom: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 30px;
}

.contacts-form__input::placeholder,
.contacts-form__textarea::placeholder {
  color: #cccccc;
  font-size: 16px;
}

.contacts-form__textarea {
  resize: none;
}

.contacts-form__btn {
  margin-left: auto;
}

.contacts-form br {
  display: none;
}

/* prices.html */

.prices {
  margin-bottom: -80px;
}

.prices__h {
  margin-bottom: 45px;
}

.prices table {
  border: 1px solid #555;
  width: 100%;
  font-size: 14px;
  color: #999;
}

.prices thead {
  border: 1px solid #555;
}

.prices td {
  padding: 10px;
  padding-left: 16px;
  border-left: 1px solid #555;
}

/* services.html */

.services-list__item {
  margin-bottom: 80px;
}

.service {
  padding-right: 50%;
  position: relative;
  min-height: 464px;
}

.service:nth-child(even) {
  padding-right: 0;
  padding-left: calc(50% + 40px);
}

.service:nth-child(even) .service__thumb {
  left: 0;
}

.service__name {
  margin-bottom: 35px;
}

.service__text {
  font-size: 16px;
  margin-bottom: 60px;
}

.service__action {
  display: flex;
  align-items: center;
}

.service__subscribe {
  margin-right: 20px;
}

.service__thumb {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  width: calc(50% - 20px);
  max-width: 500px;
  top: 0;
  right: 20px;
  bottom: 0;
  background-color: #c50c0c;
}

.service__img {
  width: 100%;
  position: relative;
  top: -20px;
  right: -20px;
}

/* trainers.html */

.trainers__h {
  margin-bottom: 40px;
}

.trainers-list__item {
  margin-bottom: 15px;
  padding: 20px 30px;
  background-image: url(../img/trainers__trainer_bg.jpg);
  background-size: cover;
}

.trainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.trainer__wrap {
  padding-left: 50px;
  padding-right: 50px;
}

.trainer__name {
  font-size: 27px;
  font-weight: 800;
  margin-bottom: 20px;
}

.trainer__text {
  line-height: 30px;
}

/* schedule.html */

.schedule-page__h {
  margin-bottom: 40px;
}

.tabs-btns {
  margin-bottom: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tabs-btns__item {
  display: block;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: 800;
  text-transform: uppercase;
}

.tabs-btns__item + .tabs-btns__item {
  margin-left: 15px;
}

.tabs-btns__item.active-tab,
.tabs-btns__item:hover,
.tabs-btns__item:focus,
.tabs-btns__item:active {
  background-color: #c50c0c;
  transition: background-color 0.5s;
}

.tabs-btns__btn {
  color: #fff;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item.active-tab {
  display: block !important;
}

.schedule {
  border: 1px solid #555;
  width: 100%;
  font-size: 14px;
  color: #999;
  text-align: center;
}

.schedule tbody {
  border: 1px solid #555;
}

.schedule td {
  padding: 40px 10px;
  border: 1px solid #555;
}

.schedule__time {
  width: 125px;
  padding: 5px 10px;
  border-radius: 17px;
  background-color: #000;
  margin: 0 auto;
  margin-bottom: 15px;
}

.schedule__h {
  font-weight: 800;
  font-size: 21px;
  color: #fff;
  margin-bottom: 15px;
}

.schedule__trainer {
  margin-bottom: 15px;
}

.schedule__place {
  color: #6a44aa;
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .main-footer__widget {
    width: calc(50% - 20px);
  }

  .card {
    width: calc(50% - 20px);
  }

  .category {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .cards__list {
    justify-content: center;
  }

  .card {
    width: calc(83.33333333333334% - 20px);
  }

  .categories-list {
    justify-content: center;
  }

  .category {
    width: calc(83.33333333333334% - 20px);
  }
}